/* To Be Refactored */

.dotText {
  font-size: 2rem;
}

.logoText {
  color: #ffdd00;
  display: block;
  font-family: 'Open Sans', sans-serif;
  text-decoration: none;
}

.txt-#111 {
  color: #111;
}

.txt-white {
  color: white;
}

.bg-white {
  background: white;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
  height: 100%;
}

.bt-menu {
  color: #111;
}

.fm-navbar-menu {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  width: 100%;
  z-index: 1;
}

.white {
  background: white;
  color: #111;
}

.fm-navbar-body Link {
  font-family: 'Open Sans', sans-serif;
  text-decoration: none;
  color: #fff;
  font-size: 50px;
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}

.logo {
  color: #fff;
  padding: 0px 20px;
}

.fm-navbar-nav {
  margin: 0 10vh;
  display: grid;
}

.nav,
.bt-menu {
  justify-self: end;
}

.nav {
  margin-top: 10vh;
  display: block;
  color: #212121
}

.fm-navbar-nav a {
  display: block;
  text-align: right;
  margin: 4vw 0;
}

.fm-navbar-nav span {
  -webkit-transition: all .4s ease;
  transition: all .4s ease;
}

.bt-menu {
  cursor: pointer;
  position: absolute;
  right: 20px;
  top: 16px;
  display: none
}

.fm-navbar-social {
  position: absolute;
  bottom: 1vh;
  left: 0.5vh;
}

.fm-navbar-social ul {
  padding: 0
}

.fm-navbar-social ul>li {
  display: inline-block;
  padding: 0 1vw;
  margin: 0 1vw;
  color: #212121;
  text-transform: uppercase;
}

.fm-navbar-social ul>li a {
  font-size: 2vw;
  text-align: left;
}

.nav-lg {
  display: grid;
  padding: 0px 15px;
}

.nav-lg div {
  position: absolute;
  right: 20px;
  top: 20px;
  font-family: 'Open Sans', sans-serif;
}

.nav-lg a {
  font-size: 14px !important;
  padding: 10px 20px;
  text-transform: uppercase;
  color: #212121;
}

.nav-lg a {
  color: white;
  cursor: pointer;
  text-decoration: none;
  -webkit-transition: all .2s ease;
  transition: all .2s ease;
}

.is-active, 
.nav-lg a:hover {
color: #ffdd00 !important;
}

@media screen and (max-width:769px) {
  .bt-menu {
    display: block
  }

  .nav-lg {
    display: none;
  }
}

@media screen and (min-width:769px) {
  .fm-navbar-nav {
    display: none
  }

  .bg-white {
    background: none;
  }
}
body {
    margin: 0;
    font-family: 'Open Sans', sans-serif;
}

.container {
    position: relative;
  }

.page-enter {
    opacity: 0;
    -webkit-transform: scale(1.1);
            transform: scale(1.1);
}

.page-enter-active {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
    -webkit-transition: opacity 400ms ease, -webkit-transform 400ms ease;
    transition: opacity 400ms ease, -webkit-transform 400ms ease;
    transition: opacity 400ms ease, transform 400ms ease;
    transition: opacity 400ms ease, transform 400ms ease, -webkit-transform 400ms ease;
}

.page-exit {
    opacity: 1;
    -webkit-transform: scale(1);
            transform: scale(1);
}

.page-exit-active {
    opacity: 0;
    -webkit-transform: scale(0.9);
            transform: scale(0.9);
    -webkit-transition: opacity 400ms ease, -webkit-transform 400ms ease;
    transition: opacity 400ms ease, -webkit-transform 400ms ease;
    transition: opacity 400ms ease, transform 400ms ease;
    transition: opacity 400ms ease, transform 400ms ease, -webkit-transform 400ms ease;
}
